<template>
    <div>
        <button @click="count++">Count is: {{ count }}</button>
        <h2>姓名：{{name}}</h2>
        <h2>年龄：{{age}}</h2>
        <button @click="sayInfo">显示信息</button>
    </div>
    
    
</template>

<script>
import {ref} from 'vue';
export default {
  data() {
    return {
      count: 0
    }
  },
  setup(){
    let name = ref("阿宝")
    let age = ref(18)
    function sayInfo(){
        name.value = '朝朝'
        age.value = '两岁了'
    }
    return{
        name,age,sayInfo
    }
  }
}
</script>

<style scoped>
button {
  font-weight: bold;
}
</style>